<template>
  <div>
    <el-tabs v-model="defaultYear" type="card">
      <el-tab-pane :label="`${c.CurrentYear}`" :name="c.CurrentYear">
        <div style="display:inline-block;width:70%;vertical-align:top;">
          <div class="top-add">
            <el-button @click="handleAdd" type="primary" plain small></el-button>
          </div>
          <div class="top-container">
            <el-tag type="success">9</el-tag>
          </div>
        </div>
        <div style="display:inline-block;text-align:left;width:25%">
          <el-card class="box-card">
            <div style="color: red;font-size: 16px;font-weight: 700"></div>
            <div>1.</div>
            <div>2.</div>
          </el-card>
        </div>
        <el-dialog
          title=""
          :visible.sync="dialogVisible"
        >
          <el-form :model="formData" ref="formData">
            <el-form-item v-show="false">
              <el-input auto-complete="off" v-model="formData.id"></el-input>
            </el-form-item>
            <el-form-item label=":" >
              <el-select placeholder="" v-model="formData.type">
                <el-option v-for="index in 3" :label="types[index]" :value="index"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label=":"
              :rules="[
                { required: true, message: ''}
              ]"
              prop="name">
              <el-input auto-complete="off" v-model="formData.name"></el-input>
            </el-form-item>
            <el-form-item
              label=":"
              :rules="[
                { required: true, message: ''}
              ]"
              prop="description">
              <el-input auto-complete="off" :maxlength="1000" placeholder="1000" type="textarea" v-model="formData.description"></el-input>
            </el-form-item>
            <el-form-item
              label=":"
              :rules="[
                { required: true, message: ''}
              ]"
              prop="areaUnit">
              <el-input auto-complete="off" v-model="formData.areaUnit"></el-input>
            </el-form-item>
            <el-form-item
              label="/:"
              prop="num"
              :rules="[
                { required: true, message: ''},
                { type: 'number', message: ''}
              ]">
              <el-input auto-complete="off" v-model.number="formData.num"></el-input>
            </el-form-item>
            <el-form-item
              label=":"
              prop="trzj"
              :rules="[
                { required: true, message: ''},
                { type: 'number', message: ''}
              ]">
              <el-input auto-complete="off" v-model.number="formData.trzj"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="confirmAdd" v-if="action=='add'"> </el-button>
            <el-button type="primary" @click="confirmEdit" v-if="action=='edit'"> </el-button>
          </div>
        </el-dialog>
        <el-table
        :data="tableData"
        border
        style="width: 95%;margin-left:20px">
          <el-table-column
            width="50"
            label="">
            <template slot-scope="scope">{{ scope.$index + 1 }}</template>
          </el-table-column>
          <el-table-column
            prop="province"
            sortable
            label="">
          </el-table-column>
          <el-table-column
            sortable
            label="">
            <template slot-scope="scope">
              {{types[scope.row.type]}}
            </template>
          </el-table-column>
          <el-table-column
            prop="name"
            sortable
            label="">
          </el-table-column>
          <el-table-column
            sortable
            label="">
            <template slot-scope="scope" v-if="scope.row.description">
              {{utils.subString(scope.row.description, 50, true)}}
            </template>
          </el-table-column>
          <el-table-column
            prop="areaUnit"
            sortable
            label="">
          </el-table-column>
          <el-table-column
            prop="num"
            sortable
            label="/">
          </el-table-column>
          <el-table-column
            prop="trzj"
            sortable
            label="">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import axios from 'axios';
import provinces from '../../config/provinces'
import utils from '../../utils'
import c from '$config/current'
import qs from 'qs'


export default {
  data () {
    return {
      tableData: [],
      c,
      formData: {
        id: null,
        province: null,
        type: null,
        name: null,
        description: null,
        areaUnit: null
      },
      utils: utils,
      types: ['0', 
        '',
        '', 
        '',
        ],
      dialogVisible: false,
      action: 'add',
      defaultYear: '2019',
      provinces: provinces.provinces
    }
  },
  mounted () {
    this.getData();
  },
  methods: {
    getData () {
      let params = {};
      let config = {
        // apibaseURL, DataSourceGateWay
        baseURL: '/api/v1',
        url: '/shehuishidian/get',
        // to methods of that instance.
        method: 'get',
        // paramsget url
        params: params
      };
      axios.request(config)
      .then(result => {
          let data = result.data;
        console.log(data);
        this.tableData = data.data;
      }, err => {
        this.$alert(err.message);
      });
    },
    resetForm () {
      this.formData = {
        id: null,
        province: null,
        type: null,
        name: null,
        description: null,
        areaUnit: null
      }
    },
    handleEdit (index, rowData) {
      console.log(index, rowData, this.formData)
      this.resetForm();
      this.action = 'edit';
      Object.keys(rowData).forEach(item => {
        this.formData[item] = rowData[item];
      })
      this.dialogVisible = true;
    },
    handleDelete (index, rowData) {
      console.log(index, rowData)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let config = {
          baseURL: '/api/v1',
          paramSerializerJQLikeEnabled: true,
          url: '/shehuishidian/delete',
          method: 'post',
          data: qs.stringify({id: rowData.id})
        };
        axios.request(config)
        .then(result => {
          let data = result.data;
          if (Object.keys(data) == 0) {
            this.$message({
              message: '',
              type: 'success'
            });
            this.tableData.splice(index, 1);
          } else {
            this.$message({
              message: '',
              type: 'error'
            });
          }
        }, err => {
          this.$alert(err.message);
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    },
    handleAdd () {
      this.resetForm();
      this.action = 'add';
      this.dialogVisible = true;
    },
    confirmAdd () {
      let data = this.formData;
      let config = {
        // apibaseURL, DataSourceGateWay
        baseURL: '/api/v1',
        url: '/shehuishidian/add',
        // to methods of that instance.
        method: 'post',
        // datapost http
        data: qs.stringify(data)
      };
       this.$refs.formData.validate((valid) => {
        if (valid) {
          axios.request(config)
          .then(result => {
          let data = result.data;
            console.log(data);
            if (data.id) {
              this.$message({
                message: '',
                type: 'success'
              });
              this.formData.id = data.id;
              this.formData.province = data.province;
              this.tableData.unshift(this.formData);
              this.dialogVisible = false;
            } else {
              this.$message({
                message: '',
                type: 'error'
              });
            } 
          }, err => {
            this.$alert(err.message);
          });
        }
      })
    },
    confirmEdit () {
      let data = this.formData;
      let config = {
        baseURL: '/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/shehuishidian/update',
        method: 'post',
        data: qs.stringify(data)
      };
      axios.request(config)
      .then(result => {
          let data = result.data;
        if (Object.keys(data) == 0) {
          this.$message({
            message: '',
            type: 'success'
          });
          this.tableData.forEach((item, index) => {
            if (this.formData.id == item.id) {
              this.tableData.splice(index, 1, this.formData);
            }
          })
          console.log(this.tableData);
          this.dialogVisible = false;
        } else {
          this.$message({
            message: '',
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    }
  }
}

</script>
<style lang="less">
  .dialog-footer {
    text-align:center;
  }
</style>


